// открытие footer
.footer {
    margin-top: 73px;
    font-size: 14px;
    background: #F7F7F7;
    padding: 15px 0;
    .btn-modal {
        margin-top: -205px;
        margin-right: 10px;
    }
    a {
        text-decoration: underline;
    }
    .one {
        p {
            margin-top: 15px;
        }
    }
    .two {
        p {
            &:nth-child(1) {
                margin: 9px 0 0 13px;
            }
            &:nth-child(2) {
                margin: 19px 10px 0 13px;;
            }
        }
    }
    .three {
        columns: 2;
        padding: 0 0px 0 45px;
        line-height: 2.85;
        display: flex;
        justify-content: space-between;
        a {
            
        }
    }
    .four {
        padding-right: 0;
        float: right;
        .phone {
            margin-top: 7px;
            a {
                font-size: 16px;
                text-decoration: none;
            }
        }
        .glyanets {
            text-align: right;
            margin-top: 16px;
            p {
                margin-top: 33px;
                font-size: 10px;
            }
        }
    }
}
@media screen and (min-width: 320px) and (max-width:991.98px) {
    .footer {
        text-align: center;
        .three {
            padding: 0 15px;
            justify-content: space-evenly;
        }
        .phone {
            text-align: center;
        }
        .glyanets {
            text-align: left !important;
            p {
                margin-top: 20px !important;
            }
        }
        .btn-modal {
            margin-top: -91px;
            margin-right: 28px;
            width: 100px;
            height: 100px;
            img {
                margin-top: 5px;
            }
            p {
                font-size: 12px;
            }
        }
    }
}
@media screen and (min-width:992px) and (max-width:1400px) {
    .footer {
        .phone {
            float: right;
        }
    }
}
// закрытие footer