// открытие products
.products {
    margin-top: 30px;
    h2 {
        width: 100%;
        text-align: center;
    }
}
.products-items {
    display: flex;
    margin: 0 auto;
    margin-top: 38px;
}
.products-item {
    display: flex;
    border: 1px solid #DCE1E6;
    box-sizing: border-box;
    box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.05);
    margin: 0 15px;
    width: 33.3%;
    img {
        margin: 25px 30px;
    }
}
.products-info {
    margin-bottom: 15px;
    .products-info-link {
        display: flex;
        flex-direction: column;
        line-height: 2.6;
        columns: 1;
    }
    .products-info-link2 {
        columns: 2;
        line-height: 2.6;
        a {
            margin-right: 15px;
        }
    }
    h4 {
        font-weight: 700;
        text-transform: uppercase;
        margin: 25px 0 15px 0;
        text-align: left;
    }
    a {
        font-size: 14px;
    }
}
.products-img {
    margin-right: 10px;
}
.btn-modal {
    background: #1AB0CE;
    box-shadow: 0px 4px 14px rgba(0, 0, 0, 0.05);
    width: 110px;
    height: 110px;
    border-radius: 50%;
    margin-top: -60px;
    margin-right: 10px;
    float: right;
    span {
        font-size: 14px;
    }
}
@media screen and (min-width:320px) and (max-width:991.98px) {
    .products {
        margin-top: 55px;
    }
    .products-items {
        flex-wrap: wrap;
        .products-item {
            width: 100%;
            margin-bottom: 15px;
        }
    }
    .products-info a {
        font-size: 10px;
    }
    .products-info-link2 {
        columns: 1 !important;
        display: block;
    }
    .products-info-link {
        display: block !important;
        a {
            margin-right: 15px;
        }
    }
}
@media screen and (min-width:992px) and (max-width:1366px) {
    .products {
        margin-top: 55px;
    }
}
// закрытие products