// открытие news
.news {
    text-align: center;
    margin-top: 33px;
    h2 {
        width: 100%;
    }
    .hide {
        display: none;
    }
}
.news-item {
    display: flex;
    margin-top: 43px;
    margin-bottom: 73px;
    justify-content: center;
    .card {
        margin: 0 10px;
        &:hover {
            box-shadow: 0px 14px 24px rgba(0, 0, 0, 0.05), 0px -1px 0px #1AB0CE;
        }
    }
}
.news-btn {
    margin: 0 auto;
    a {
        border: 1px solid #1AB0CE;
        box-sizing: border-box;
        border-radius: 2px;
        background: #fff;
        color: #1AB0CE;
        padding: 7px 20px;
        text-decoration: none;
        &:hover {
            background: #1AB0CE;
            color: #fff;
        }
    }
}
@media screen and (min-width: 320px) and (max-width:991.98px) {
    .news-item {
        flex-wrap: wrap;
        .card {
            margin: 0 15px 15px 15px;
        }
    }
}
// закрытие news