body {
    font-family: 'Clear Sans', arial;
    max-width: 1920px;
    margin: 0 auto;
    color: #3A4047;
}
.container {
    max-width: 1200px !important;
}
a {
    color: #3A4047;
}
h1 {
    font-size: 57px;
    text-align: center;
}
h2 {
    font-size: 36px;
    text-align: center;
}
h3 {
    font-size: 24px;
    text-align: center;
}
h4 {
    font-size: 16px;
    text-align: center;
}
// открытие header
.header {
    display: flex;
    margin-bottom: 10px;
    margin-top: 15px;
}
.logo {

}
.text {
    text-align: center;
    margin: 0 32px;
    p {
        margin-bottom: 5px;
        &:nth-child(2) {
            font-weight: 700;
            font-size: 14px;
        }
    }
}
.phones {
    margin: 0px -17px;
}
.link {
    margin: 5px 10px;
    a {
        color: #3A4047;
        text-decoration: underline;
        &:nth-child(2) {
            float: right;
        }
    }
}
.phone {
    text-align: right;
    margin-top: 15px;
    .btn {
        width: 20px;
        height: 20px;
        background: transparent;
        border: none;
        img {
            margin: -20px 0px 0 -10px;
        }
    }
    a {
        font-weight: 700;
        img {
            margin: 0 22px 0 0;
        }
    }
}
.open {
    display: flex !important;
    flex-direction: column;
    width: 220px;
    height: 170px;
    position: absolute;
    background: #fff;
    z-index: 9;
    right: 20px;
    box-shadow: 0px -19px 40px rgba(0, 0, 0, 0.05);
    margin-top: 20px;
    &::after {
        content: "";
        width: 20px;
        height: 20px;
        background: #fff;
        transform: rotate(45deg);
        margin: -160px 0 0 180px
    }
    a {
        text-align: center;
        font-weight: 400;
        line-height: 2.7;
        &:nth-child(1) {
            margin-top: 20px;
        }
    }
}
.open2 {
    display: flex !important;
    flex-direction: column;
    width: 220px;
    height: 170px;
    position: absolute;
    background: #fff;
    z-index: 9;
    right: 20px;
    box-shadow: 0px -19px 40px rgba(0, 0, 0, 0.05);
    margin-top: 20px;
    &::after {
        content: "";
        width: 20px;
        height: 20px;
        background: #fff;
        transform: rotate(45deg);
        margin: -160px 0 0 180px
    }
    a {
        text-align: center;
        font-weight: 400;
        line-height: 2.7;
        &:nth-child(1) {
            margin-top: 20px;
        }
    }
}
.open3 {
    display: block !important;
}
.hide {
    display: none;
}
.hide2 {
    display: none;
}
.badge {
    background: #1AB0CE;
    border-radius: 50%;
    padding: 8px 10px;
    color: #fff;
    top: -15px !important;
    left: -15px;
}
.cart {
    padding: 0;
    margin: 11px 0 0 5px;
    .btn {
        background: transparent;
        border: none;
        width: 80px;
        &:hover {
            background: #e7eaed;
        }
    }
}
hr {
    width: 100%;
    background: #E5E5E5;
    margin: 0;
}
.navbar {
    width: 100%;
    padding: 4px 0;
}
.navbar-nav {
    display: flex;
    justify-content: space-around;
    width: 100%;
    button {
        background: transparent;
        border-radius: 0px;
        margin: -10px 0 -3px 0;
        padding: 17px 0;
        line-height: 10px;
        border: none;
        cursor: pointer;
        border-top: 2px solid transparent;
        &:hover {
            color: #1AB0CE;
            border-top: 1px solid;
            line-height: 8px !important;
        }
    }
    .nav-link {
        text-align: center;
        text-transform: uppercase;
        font-weight: 700;
        padding-right: 20px !important;
        padding-left: 20px !important;
        &:hover {
            color: #1AB0CE;
            border-top: 2px solid;
            margin: -10px 0 -10px 0;
            line-height: 40px;
        }
    }
    hr {
        transform: rotate(90deg);
        width: 13px;
        position: relative;
        top: 20px;
    }
}
.form-hide {
    display: none;
    input {
        position: absolute;
        right: 60px;
        margin-top: -21px;
    }
}
.search {
    &:hover {
        margin: -10px 0 -3px 0 !important;
    }
}
@media screen and (min-width:320px) and (max-width: 767.98px) {
    .header {
        flex-direction: column;
    }
    .link {
        text-align: center;
        a {
            width: 100%;
            margin: 10px 0;
        }
    }
    .cart {
        margin: 15px auto;
        text-align: center;
    }
    .navbar-toggler {
        background: #ececec;
        border: 1px solid #000;
        margin-left: 15px;
    }
    .navbar-nav {
        hr {
            display: none;
        }
    }
    .text {
        margin: 0;
    }
    .phones {
        margin: 0;
        .phone {
            text-align: center;
        }
    }
    .nav-item {
        margin: 0 auto;
    }
    .form-hide {
        margin: 0 15px;
        text-align: center;
        input {
            position: relative;
            right: 0;
            margin-top: -5px;
        }
    }
    .fas {
        font-size: 31px;
    }
    .logo {
        text-align: center;
    }
}
@media screen and (min-width:768px) and (max-width: 991.98px) {
    .header {
        flex-wrap: wrap;
        justify-content: center;
    }
    .link {
        text-align: center;
        a {
            width: 100%;
            margin: 10px 0;
        }
    }
    .cart {
        margin: 15px auto;
        text-align: left;
    }
    .navbar-toggler {
        background: #ececec;
        border: 1px solid #000;
        margin-left: 15px;
    }
    .navbar-nav {
        hr {
            display: none;
        }
    }
    .text {
        margin: 0;
    }
    .phones {
        margin: 0;
        .phone {
            text-align: center;
        }
    }
    .nav-item {
        margin: 0 auto;
    }
    .form-hide {
        margin: 0 15px;
        text-align: center;
        input {
            position: relative;
            right: 0;
            margin-top: -5px;
        }
    }
    .fas {
        font-size: 31px;
    }
    .logo {
        text-align: center;
    }
    .open {
        right: 83px;
    }
}
@media screen and (min-width:992px) and (max-width:1199.98px) {
    .phone {
        float: left;
        .open {
            right: 60px;
        }
    }
    .phones {
        margin: 0px -75px 0 0;
    }
    .nav-link {
        font-size: 13px;
    }
    .link {
        a {
            width: 100%;
        }
    }
}
@media screen and (min-width:1200px) and (max-width:1250px) {
    .cart {
        padding: 0;
        margin: 10px 0 0 0px;
        max-width: 80px !important;
    }
    .phones {
        margin: 0 0 0 -50px;
    }
}
// закрытие header