// открытие banner
.banner {
    background: url("../img/background.svg") center no-repeat;
    height: 500px;
    .btn-modal {
        background: #1AB0CE;
        border-radius: 2px;
        width: 200px;
        height: 48px;
        color: #FFFFFF;
        margin: 0;
        float: none;
        border: none;
        &:hover {
            opacity: 0.8;
        }
    }
    .row {
        display: flex;
        flex-direction: column;
        text-align: center;
        h1 {
            color: #FFFFFF;
            margin: 14% 0 0 0;
        }
        h3 {
            color: #FFFFFF;
            margin-bottom: 24px;
        }
        hr {
            border: 1px solid #FFFFFF;
            width: 120px;
            margin: 4px auto 18px auto;
        }
    }
}
@media screen and (min-width: 320px) and (max-width: 991.98px) {
    .banner h1 {
        font-size: 48px;
        .row {
            margin: 0;
        }
    }
}
// закрытие banner